
.shading-bar {
    position: absolute;
    top: -80px;
    left: calc(50% - 260px);
    width: 500px;
    padding: 10px 15px;
    border-radius: 8px;
    border: 1px solid #ccc;
    background-color: #e0fee9;
    z-index: 3;
    opacity: 0;
    transition: all 0.2s;
}

.shading-bar.active {
    opacity: 1;
    top: 20px;
}

.shading-bar .shading-item {
    width: 80px;
    margin: 0px 12px;
}

.shading-bar .shading-item * {
    font-size: 14px;
}

.shading-bar .shading-item .value {
    font-weight: 500;
    color: #f00;
}

.shading-bar .shading-item input{
    width: 100%;
}

.shading-bar .button {
    background-color: #6d8;
    color: white;
    font-size: 16px;
    font-weight: 700;
}