.page-wrapper .main-page-inner {padding: 0;    width: 100%; height: 100%;}
.page-wrapper .main-page-inner.active{z-index: 2;}

.containerMain { width: 100%;; height: 100%; background-color: grey; }

.tool-bar {background-color: #e0fee9; height: 100%; border-right: 1px solid #00000088;}

.main-page-inner .setting {
    position: absolute;
    height: 100%;
    background-color: #e0fee9;
    border-bottom: 1px solid #00000088;
    transition: all 0.3s;
    border-left: solid 1px #00000088;
    border-right: solid 1px #00000088;
}
.main-page-inner .setting.setting-left {
    left: 0;
    width: 150px;
    transform: translateX(-100%);
}
.main-page-inner .setting.setting-right {
    right: 0;
    width: 300px;
    transform: translateX(100%);
}
.main-page-inner .setting.show {
    transform: translateX(0);
}
.main-page-inner .setting * {
    color:black;
}
.main-page-inner .setting-wrapper {
    position: relative;
    width: 100%;
    padding: 0px;
    height: 100%;
}
.main-page-inner .setting .setting-arrow {
    position: absolute;
    top: 0px;
    width: 30px;
    height: 60px;
    border-radius: 4px;
    transition: all 0.3s;
    background-color: #e0fee9;
    cursor: pointer;
    z-index: 1;
}
.main-page-inner .setting .setting-arrow i {
    transition: transform 0.25s ease;
    display: inline-block;
    font-size: 18px;
    color: #0A0;
}
.main-page-inner .setting.show .setting-arrow i {
    transform: rotate(180deg);
}

.main-page-inner .setting.setting-left .setting-arrow {
    right: -32px;
}
/* .main-page-inner .setting.setting-left.show .setting-arrow { right: 0px; } */
.main-page-inner .setting.setting-right .setting-arrow {
    left: -32px;
}
/* .main-page-inner .setting.setting-right.show .setting-arrow { left: 0px; } */

.main-page-inner .setting-content {
    width: calc(100% - 0px);
    padding-right: 0px;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
}
.main-page-inner .setting-content .title {
    margin-top: 10px;
    text-align: center;
    font-size: 20px;
}
.main-page-inner .setting-content .setting-part {
    position: relative;
    border-top: 1px solid #00000033;
    padding: 10px 0px;
}
.main-page-inner .setting-content .setting-part .top-button {
    position: absolute;
    top: 25px;
    right: 10px;
    height: 30px;
    font-size: 14px;
    line-height: 30px;
    padding: 0px 10px;
}
.page-wrapper .main-page-inner .setting-content .setting-part .sub-title {
    margin: 0px;
    padding: 6px 10px;
    font-weight: 700;
    font-size: 14px;
    color: #226;
}
.page-wrapper .main-page-inner .setting-content .setting-part .sub-title i {
    margin-right: 8px;
    font-size: 16px;
    color: #444;
}
.main-page-inner .setting-content .setting-part .del-btn {
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 5px 15px;
    height: auto;
    font-size: 18px;
    cursor: pointer;
}

.setting-content .set-item {margin: 10px; display: flex; align-items: center;}
.setting-content .set-item .label {width: 95px; font-size: 14px;}
.setting-content .set-item .content {flex: 1; margin: 0px 10px;}
.setting-part .set-item .content input, .setting-part .set-item .content select {
    width: calc(100% - 20px);
}
.setting-content .set-item .value {width: 55px; text-align: right; margin-right: 5px;}
.setting-content .set-item .value input {
    width: 40px;
}

.setting-part .set-item input, .setting-part .set-item select {
    border: none;
    outline: none;
    padding: 8px 10px;
    border-radius: 4px;
}

.setting-content .set-item.image-item .img {
    cursor: pointer;
    height: auto;
    transition: all 0.2s;
    box-shadow: 0 4px 10px rgba(0,0,0,0.10);
    border: 2px solid #00000088;
}
.setting-content .set-item.image-item .img.active {
    border-color: red;
}
.setting-content .set-item.image-item .img:hover {
    transform: scale(1.07);
    box-shadow: 0 6px 18px rgba(0,0,0,0.3);
}
.setting-content .set-item.house-item img {
    width: 60%;
}
.setting-content .set-item.obst-item img {
    width: 50%;
    border-radius: 8px;
}

.setting-content .set-item-block {
    display: block;
}
.setting-content .set-item-block .label{
    margin-bottom: 3px;
}
.setting-content .set-item-block .content {
    margin: 0;
}

.setting-content .set-item.analyze-obst-item {
    padding: 10px;
    background-color: #00000022;
    border-radius: 4px;
}
.setting-content .set-item .create-button .tool-tip {
    opacity: 0;
    transition: all 0.1s;
}
.setting-content .set-item .create-button:hover .tool-tip {
    opacity: 1;
}

.main-page-inner .setting .canvas-label {
    position: absolute;
    z-index: 2;
    border-radius: 4px;
    background-color: #FFFFFFCC;
    padding: 7px 10px;
    font-size: 16px;
    font-weight: 700;
    color: #008626;
}
.canvas-label.canvas-label-main {
    top: 10px;
    left: 200px;
}
.canvas-label.canvas-label-sub {
    top: 20px;
    left: 20px;
}

.setting.setting-right .containerSub {
    margin: 9px;
    width: calc(100% - 20px);
    height: 250px;
    border: 1px solid #0A0;
}

.setting.setting-right .tab-wrapper {
    border-top: 1px solid #00000044;
    width: calc(100% - 20px);
    padding: 10px 0px;
    margin: 0px 10px;
}

.setting.setting-right .tab-wrapper .tab-item {
    flex: 1;
    text-align: center;
    border-radius: 12px;
    cursor: pointer;
    padding: 5px 0px;
    transition: all 0.3s;
}
.setting.setting-right .tab-wrapper .tab-item.active {
    background-color: #6d8;
    color: white;
}