
.modal-overlay {
    position:fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color:rgba(0,0,0,0.5);
    display:flex;
    align-items: center;
    justify-content: center;
    z-index: -1;
}
.modal-overlay.show {
    z-index: 9;
}
.modal {
    background-color: #fff;
    border-radius: 6px;
    width: 480px;
    max-width: 90%;
    box-shadow: 0 10px 30px rgba(0,0,0,0.3);
}

.modal-header {
    display: flex;
    align-items: center;
    padding: 12px 16px;
    border-bottom: 1px solid #eee;
    position: relative;
}

.modal-header i {
    font-size: 16px;
    margin-right: 10px;
}
.modal-header h3 {
    margin: 0;
    font-size: 18px;
}
.modal-header button {
    position: absolute;
    right: 10px;
    background: transparent;
    border: 0;
    font-size: 24px;
    line-height: 1;
    cursor: pointer;
    transition: all 0.2s;
    border-radius: 50%;
}
.modal-header button:hover {
    transform: scale(1.1);
    background-color: #CCC;
}
.modal-body {
    padding: 32px 16px;
    max-height: calc(100vh - 500px);
    overflow-y: auto;
    overflow-x: hidden;
}

.modal-body .select-wrapper .select-item {
    margin: 8px 0px;
    padding: 8px 12px;
    border-radius: 6px;
    border: 1px solid #00000033;
    display: flex;
    justify-content: space-between;
    transition: all 0.2s;
    cursor: pointer;
}
.modal-body .select-wrapper .select-item:hover {
    background-color: #f0f0f0;
    transform: translateY(-3px);
}
.modal-body .select-wrapper .select-item.active {
    background-color: #e0fee9;
}
.modal-body .select-wrapper .select-item .name {
    font-size: 16px;
    font-weight: 500;
}
.modal-body .select-wrapper .select-item .status {
    font-size: 14px;
    font-weight: 300;
}

.modal-body .save-name {
    border: none;
    outline: none;
    padding: 8px 12px;
    width: calc(100% - 24px);
    border-radius: 8px;
    background-color: #0000001F;
}

.modal-part.result-summary .set-item .label {
    width: 140px;
}

.modal-part .set-item {margin: 10px; display: flex; align-items: center;}
.modal-part .set-item .label {width: 95px; font-size: 14px;}
.modal-part .set-item .content {flex: 1; margin: 0px 10px;}

.modal-part.result-detail {
    margin: 0px 10px;
    width: calc(100% - 20px);
}
.modal-part.result-detail .obj-item {
    margin-top: 20px;
}
.modal-part.result-detail .obj-item .obj-title {
    font-weight: 700;
    font-size: 20px;
    margin: 5px;
    color: blue;
}
.modal-part.result-detail .obj-item .main-info .main-item {
    font-weight: 500;
    margin: 5px;
    display: flex;
}
.modal-part.result-detail .obj-item .main-info .main-item .main-label {
    width: 140px;
}
.modal-part.result-detail .obj-item .main-info .main-item .main-value {
    flex: 1;
    text-align: right;
}

.modal-part.result-detail .obj-item .panel-info {
    margin-top: 20px;
    border-top: 1px solid #00000022;
}
.modal-part.result-detail .obj-item .panel-info .panel-item {
    display: flex;
    margin: 3px 0px;
    border-bottom: 1px solid #00000022;
}
.modal-part.result-detail .obj-item .panel-info .panel-item div {
    flex: 1;
    font-size: 14px;
}
.modal-part.result-detail .obj-item .panel-info .panel-item .panel-name {
    text-align: left;
}
.modal-part.result-detail .obj-item .panel-info .panel-item .panel-shade {
    text-align: right;
}
.modal-part.result-detail .obj-item .panel-info .panel-item .panel-real-capacity {
    text-align: right;
}

.modal-footer {
    display:flex;
    justify-content:flex-end;
    gap:8px;
    padding:12px;
    border-top:1px solid #eee;
}

.modal-footer .button {
    height: 32px;
    line-height: 32px;
}
.modal-footer .button.highlight {
    background-color: #e0fee9;
}

.project-select-wrapper {
    max-height: 150px;
    overflow-y: auto;
}

.gm-bundled-control .gm-control-active,
.gm-bundled-control .gm-control {
    display: none !important;
}
