
.page-wrapper .page-content.page-container .transform-help-wrapper {
    position: absolute;
    bottom: -60px;
    left: calc(50% - 160px);
    width: 220px;
    background-color: white;
    border: 1px solid #00000022;
    padding: 10px;
    background-color: #e0fee9;
    border-radius: 10px;
    z-index: 3;
    transition: all 0.3s;
}
.page-wrapper .page-content.page-container .transform-help-wrapper.active {
    /* z-index: 3; */
    bottom: 20px;
}

.page-wrapper .page-content.page-container .transform-help-wrapper .transform-help-item {
    margin: 0px 8px;
    width: 34px;
    height: 34px;
    border-radius: 6px;
    cursor: pointer;
    border: 1px solid #00000022;
    background-color: white;
    transition: all 0.2s;
}
.page-wrapper .page-content.page-container .transform-help-wrapper .transform-help-item.non-border {
    background-color: #00000000 !important;
}
.page-wrapper .page-content.page-container .transform-help-wrapper .transform-help-item:hover {
    background-color: #66DD8888;
}
.page-wrapper .page-content.page-container .transform-help-wrapper .transform-help-item.active {
    background-color: #6D8;
}
.page-wrapper .page-content.page-container .transform-help-wrapper .transform-help-item.hide {
    display: none;
}
.page-wrapper .page-content.page-container .transform-help-wrapper .transform-help-item .label {
    font-size: 16px;
}
.page-wrapper .page-content.page-container .transform-help-wrapper .transform-help-item .key {
    font-weight: bold;
    margin-left: 10px;
}
